

.Header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
//    margin: 1rem;
    border: 5px solid #ff0f0f;
    box-shadow: 0 4px 4px 0 #1006f5;
    h3{
        margin: 1rem;
        border: 1px solid #ff0f0f;
        display: flex;
        justify-Content: center;
    }
}


.Body {
    margin: 1rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border: 5px solid #f50ecf;
    box-shadow: 0 4px 4px 0 #147be3;
}

.LeftBody {
    justify-content: center;
    margin: 1rem;
    box-shadow: 0 4px 4px 0 #147be3;
    border: 5px solid #147be3;
}
.MidBody {
    justify-content: center;
    margin: 1rem;
    box-shadow: 0 4px 4px 0 #147be3;
    border: 5px solid #4cf50e;

    width: 100%;
}

.RightBody {
    justify-content: center;
    margin: 1rem;
    box-shadow: 0 4px 4px 0 #147be3;
    border: 5px solid #239a7a;
}
.BottomBody {
    justify-content: center;
    margin: 1rem;
    box-shadow: 0 4px 4px 0 #147be3;
    border: 5px solid #239a7a;
}
